.productCard-container {
  width: 20rem;
  margin: 0.7rem;
  display: flex;
  flex-direction: column;
}
.productCard-img {
  padding: 2rem;
  width: 250px;
  height: 250px;
  align-self: center;
}
.productCard-img img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}
.productCard-body {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}
.productCard-name {
  flex: 0.7 1;
  font-size: 1rem;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.7);
}
.productCard-price {
  flex: 0.2 1;
  color: rgba(0, 0, 0, 0.7);
}
.productCard-btn {
  margin-top: 10px;
  width: 9rem;
  flex: 0.1 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 500;
}
.productCard-btn svg {
  margin-right: 0.5rem;
}

