.pageItem {
  background-color: rgb(255, 255, 255);
  list-style: none;
  border: 0.1px solid rgba(0, 0, 0, 0.1);
  padding: 0.5rem 1.2rem;
  transition: all 0.3s;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 500;
  margin-top: 3rem;
}
.pageItem:first-child {
  border-radius: 5px 0 0 5px;
}
.pageItem:last-child {
  border-radius: 0 5px 5px 0;
}
.pageLink {
  text-decoration: none;
  font-size: 1.3rem;
  color: #0088ff;
  transition: all 0.3s;
}
.pageItem:hover {
  background-color: #0088ff;
}
.pageItem:hover .pageLink {
  color: white;
}
.pageItemActive {
  background-color: #0088ff;
}
.pageLinkActive {
